@charset "UTF-8";
/************
RESETS
************/
html {
  box-sizing: border-box;
  color: #444;
  font: 12pt 'Open Sans', Arial;
  font-weight: 400; }

*, *::before, *::after {
  box-sizing: inherit;
  color: inherit;
  font: inherit; }

.background-mogs-container {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  overflow: hidden;
  width: 100%;
  height: 100%; }

.background-mogs-container .mog-img {
  margin: 0.7em;
  position: relative;
  /* display: inline-block; */
  background-size: cover;
  width: 100px;
  height: 100px;
  border-radius: 50%;
  border: solid #FEBF44 2px;
  -webkit-box-shadow: 0px 10px 30px -8px rgba(0, 0, 0, 0.75);
  -moz-box-shadow: 0px 10px 30px -8px rgba(0, 0, 0, 0.75);
  box-shadow: 0px 10px 30px -8px rgba(0, 0, 0, 0.75); }

body {
  margin: 0;
  padding: 0;
  background-color: white; }

navbar {
  width: 100%;
  /*position: absolute;*/
  top: -16px; }

navbar ul {
  display: flex;
  /* justify-content: space-between; */
  text-align: center;
  margin: 0;
  padding: 0;
  background-color: red; }

navbar ul li {
  list-style: none;
  flex: 1; }

.nav-selected {
  border-bottom: solid yellow 5px; }

a {
  text-decoration: none; }

.navtab {
  flex: 1;
  height: 4em;
  padding-top: 1em;
  margin: 0; }

/*******
LOGIN SCREEN CSS
****/
.login-container-content {
  position: relative;
  width: 25em;
  height: 25em;
  /*overflow: hidden;*/ }

.login-container {
  overflow: hidden;
  width: 25em;
  background: rgba(238, 238, 238, 0.95);
  height: 25em;
  position: absolute;
  border-radius: 50%;
  top: 50%;
  left: 50%;
  vertical-align: baseline;
  transform: translate(-50%, -50%);
  -webkit-box-shadow: 0px 8px 54px -11px black;
  -moz-box-shadow: 0px 8px 54px -11px black;
  box-shadow: 0px 8px 54px -11px black; }

.register {
  position: absolute;
  left: 0em;
  top: 0em;
  border-bottom: solid rgba(0, 0, 0, 0.8) 1px;
  width: 200px;
  height: 4.4em;
  text-align: center;
  border-right: solid rgba(0, 0, 0, 0.8) 1px; }

/*
.register:hover {
  background-color: #E96308;
}
*/
.register > a {
  margin: 2.7em 0 0 4em;
  display: inline-block; }

.login {
  position: absolute;
  left: 200px;
  top: 0em;
  border-bottom: solid rgba(0, 0, 0, 0.8) 1px;
  width: 201px;
  height: 70px; }

/*
.login:hover {
    background-color: #E96308;
}
*/
.login > a {
  margin: 45px 0 0 50px;
  display: inline-block; }

.login-selected {
  background-color: #FC881D; }

.register-login {
  display: flex;
  justify-content: space-around; }

.logo-container {
  text-align: center;
  position: absolute;
  top: 4.4em;
  left: 50%;
  transform: translatex(-50%); }

.logo-container > img {
  width: 172px;
  height: 172px; }

form {
  position: absolute;
  /*display: flex;*/
  padding: 1em;
  text-align: center; }

.fields-container {
  width: 200px;
  height: 110px;
  display: -webkit-flex;
  display: -ms-flexbox;
  display: flex;
  position: absolute;
  -webkit-flex-direction: column;
  -ms-flex-direction: column;
  flex-direction: column;
  -webkit-justify-content: space-around;
  -ms-flex-pack: distribute;
  justify-content: space-around;
  top: 240px;
  left: 100px; }

#remember-me {
  position: absolute;
  top: 334px;
  left: 140px;
  width: 110px;
  font-size: 11px; }

.btn-submit {
  position: absolute;
  height: 2.8em;
  top: 22em;
  left: 4.5em;
  width: 16em; }

.btn-submit button {
  position: absolute;
  height: 50px;
  /* top: 31em; */
  left: 50%;
  border-top: solid rgba(0, 0, 0, 0.8) 1px;
  width: 260px;
  background: rgba(253, 195, 70, 0.95);
  transform: translateX(-50%); }

