.bet-pod-mogs {
    padding: 0 0.7em;
    width: 100%;
    height: 90%;
    display: -webkit-flex;
    display: -ms-flexbox;
    display: flex;
    -webkit-flex-direction: row;
    -ms-flex-direction: row;
    flex-direction: row;
    -webkit-flex-wrap: wrap;
    -ms-flex-wrap: wrap;
    flex-wrap: wrap;
    -webkit-align-content: flex-start;
    -ms-flex-line-pack: start;
    align-content: flex-start;
    justify-content: center;
}

.bet-pod {
    overflow: scroll;
}

.mog-inventory {
	flex: 4;
	background-color: orange;
    overflow: scroll;
}

.mog-container {
    display: flex;
    text-align: center;
}

.bet-pod-container {
    overflow: scroll;
}

.mog-img {
    margin: 0.5em;
    position: relative;
    /* display: inline-block; */
    background-size: cover;
    width: 50px;
    height: 50px;
    border-radius: 50%;
    border: solid black 1px;
}

.mog {
    margin: 0.2em;
    position: relative;
    /* display: inline-block; */
    background-size: cover;
    width: 50px;
    height: 50px;
    border-radius: 50%;
    border: solid black 1px;
}

.mog-inv-container {
    overflow: scroll;
}

.inv-mogs {
    display: -webkit-flex;
    display: -ms-flexbox;
    display: flex;
    -webkit-flex-wrap: wrap;
    -ms-flex-wrap: wrap;
    flex-wrap: wrap;
    -webkit-flex-direction: row;
    -ms-flex-direction: row;
    flex-direction: row;
    padding: 0px 1em;
    width: 100%;
    height: 90%;
    -webkit-justify-content: center;
    -ms-flex-pack: center;
    align-content: flex-start;
    justify-content: flex-start;
}

.game-field {
    margin: 3em auto;
    min-width: 480px;
    width: 85%;
    height: 600px;
    background-color: gray;
    position: relative;
}

.game-details h3 {
    padding-left: 3em;
}
.user-mogs, .won-mogs{
    margin: auto;
    width: 85%;
    display: flex;
    flex-wrap: wrap;
    justify-content: left;
    border-bottom: 1px solid black;
}

/****
SLAMMER CSS
***/
.slammer {
    margin: 25em auto 0 auto;
    width: 400px;
    position: relative;
}

.upper  {
    position: absolute;
    top: -170px;
    height: 200px;
    width: 400px;
    background-color: orange;
    border-radius: 400px 400px 0 0;
    -moz-border-radius: 400px 400px 0 0;
    -webkit-border-radius: 400px 400px 0 0;
}

.upper:hover, .lower:hover {
    background-color: yellow;
}

.lower{
    height: 200px;
    position: absolute;
    width: 400px;
    background-color: blue;
    border-radius: 0 0 400px 400px;
    -moz-border-radius: 0 0 400px 400px;
    -webkit-border-radius: 0 0 400px 400px;
    top: 30px;
}

.gates {
    height: 60px;
    width: 400px;
    background-color: white;
    position: relative;
}

.enter {
    float: left;
    position: absolute;
    height: 60px;
    width: 375px;
    background-color: green;
    z-index: 1;
}

.exit {
    float: right;
    position: absolute;
    height: 60px;
    width: 25px;
    background-color: red;
    right: 0px;
    z-index: 1;
}

::-webkit-input-placeholder {
   text-align: center;
}

:-moz-placeholder { /* Firefox 18- */
   text-align: center;  
}

::-moz-placeholder {  /* Firefox 19+ */
   text-align: center;  
}

:-ms-input-placeholder {  
   text-align: center; 
}

